%extend_1 {
	position: absolute;
	pointer-events: none;
	z-index: 2;
}

.mapboxgl-map {
	font: 12px/20px Helvetica Neue, Arial, Helvetica, sans-serif;
	overflow: hidden;
	position: relative;
	text-align: left;

	&:-webkit-full-screen {
		width: 100%;
		height: 100%;
	}

	&:hover .mapboxgl-popup-track-pointer {
		display: flex;
	}

	&:active .mapboxgl-popup-track-pointer {
		display: none;
	}
}

.mapboxgl-canary {
	background-color: $color-salmon;
}

.mapboxgl-canvas-container {
	&.mapboxgl-interactive {
		cursor: grab;
		user-select: none;

		&.mapboxgl-track-pointer {
			cursor: pointer;
		}

		&:active {
			cursor: grabbing;
		}
	}

	&.mapboxgl-touch-zoom-rotate {
		touch-action: pan-x pan-y;

		.mapboxgl-canvas {
			touch-action: pan-x pan-y;
		}

		&.mapboxgl-touch-drag-pan {
			touch-action: none;

			.mapboxgl-canvas {
				touch-action: none;
			}
		}
	}

	&.mapboxgl-touch-drag-pan {
		touch-action: pinch-zoom;

		.mapboxgl-canvas {
			touch-action: pinch-zoom;
		}
	}
}

.mapboxgl-ctrl-group {
	border-radius: 4px;
	background: $white;

	button {
		width: 29px;
		height: 29px;
		display: block;
		padding: 0;
		outline: none;
		border: 0;
		box-sizing: border-box;
		background-color: transparent;
		cursor: pointer;

		&.mapboxgl-ctrl-compass {
			cursor: grab;
			user-select: none;

			&:active {
				cursor: grabbing;
			}
		}

		+button {
			border-top: 1px solid $color-alto;
		}

		&:focus {
			box-shadow: 0 0 2px 2px $color-azure_radiance;

			&:focus-visible {
				box-shadow: 0 0 2px 2px $color-azure_radiance;
			}

			&:first-child {
				border-radius: 4px 4px 0 0;
			}

			&:last-child {
				border-radius: 0 0 4px 4px;
			}

			&:only-child {
				border-radius: inherit;
			}

			&:not(:focus-visible) {
				box-shadow: none;
			}
		}
	}

	&:not(:empty) {
		box-shadow: 0 0 0 2px $black_10;
	}
}

.mapboxgl-ctrl-bottom-left {
	@extend %extend_1;
	bottom: 0;
	left: 0;

