	0% {}

	to {}
}

@-moz-keyframes mapboxgl-spin {
	0% {}

	to {}
}

@-o-keyframes mapboxgl-spin {
	0% {}

	to {}
}

@-ms-keyframes mapboxgl-spin {
	0% {}

	to {}
