@media screen {
	.mapboxgl-ctrl-attrib.mapboxgl-compact {
		min-height: 20px;
		padding: 0;
		margin: 10px;
		position: relative;
		background-color: $white;
		border-radius: 3px 12px 12px 3px;

		&:hover {
			padding: 2px 24px 2px 4px;
			visibility: visible;
			margin-top: 6px;

			.mapboxgl-ctrl-attrib-inner {
				display: block;
			}
		}

		.mapboxgl-ctrl-attrib-inner {
			display: none;
		}

		&:after {
			content: "";
			cursor: pointer;
			position: absolute;
			background-image: url($data_uri_27);
			background-color: $white_50;
			width: 24px;
			height: 24px;
			box-sizing: border-box;
			border-radius: 12px;
		}
	}

	.mapboxgl-ctrl-bottom-left>.mapboxgl-ctrl-attrib.mapboxgl-compact {
		&:hover {
			padding: 2px 4px 2px 24px;
			border-radius: 12px 3px 3px 12px;
		}

		&:after {
			bottom: 0;
			left: 0;
		}
	}

	.mapboxgl-ctrl-top-left>.mapboxgl-ctrl-attrib.mapboxgl-compact {
		&:hover {
			padding: 2px 4px 2px 24px;
			border-radius: 12px 3px 3px 12px;
		}

		&:after {
			top: 0;
			left: 0;
		}
	}

	.mapboxgl-ctrl-bottom-right>.mapboxgl-ctrl-attrib.mapboxgl-compact:after {
		bottom: 0;
		right: 0;
	}

	.mapboxgl-ctrl-top-right>.mapboxgl-ctrl-attrib.mapboxgl-compact:after {
		top: 0;
		right: 0;
	}
}

@media screen and(-ms-high-contrast: active) {
	.mapboxgl-ctrl-attrib.mapboxgl-compact:after {
		background-image: url($data_uri_28);
	}
}

@media screen and(-ms-high-contrast: black-on-white) {
	.mapboxgl-ctrl-attrib.mapboxgl-compact:after {
		background-image: url($data_uri_27);
	}
}

.mapboxgl-ctrl-attrib {
	a {
		color: $black_75;
		text-decoration: none;

		&:hover {
			color: inherit;
			text-decoration: underline;
		}
	}

	.mapbox-improve-map {
		font-weight: 700;
		margin-left: 2px;
	}
}

.mapboxgl-attrib-empty {
	display: none;
}

.mapboxgl-ctrl-scale {
	background-color: $white_75;
	font-size: 10px;
	border: 2px solid $color-mine_shaft;
	border-top: $color-mine_shaft;
	padding: 0 5px;
	color: $color-mine_shaft;
	box-sizing: border-box;
}

.mapboxgl-popup {
	position: absolute;
	top: 0;
	left: 0;
	display: flex;
	will-change: transform;
	pointer-events: none;
}

.mapboxgl-popup-anchor-top {
	flex-direction: column;

	.mapboxgl-popup-tip {
		align-self: center;
		border-top: none;
		border-bottom-color: $white;
	}
}

.mapboxgl-popup-anchor-top-left {
	flex-direction: column;

	.mapboxgl-popup-tip {
		align-self: flex-start;
		border-top: none;
		border-left: none;
		border-bottom-color: $white;
	}

	.mapboxgl-popup-content {
		border-top-left-radius: 0;
	}
}

.mapboxgl-popup-anchor-top-right {
	flex-direction: column;

	.mapboxgl-popup-tip {
		align-self: flex-end;
		border-top: none;
		border-right: none;
		border-bottom-color: $white;
	}

	.mapboxgl-popup-content {
		border-top-right-radius: 0;
	}
}

.mapboxgl-popup-anchor-bottom {
	flex-direction: column-reverse;

	.mapboxgl-popup-tip {
		align-self: center;
		border-bottom: none;
		border-top-color: $white;
	}
}

.mapboxgl-popup-anchor-bottom-left {
	flex-direction: column-reverse;

	.mapboxgl-popup-tip {
		align-self: flex-start;
		border-bottom: none;
		border-left: none;
		border-top-color: $white;
	}

	.mapboxgl-popup-content {
		border-bottom-left-radius: 0;
	}
}

.mapboxgl-popup-anchor-bottom-right {
	flex-direction: column-reverse;

	.mapboxgl-popup-tip {
		align-self: flex-end;
		border-bottom: none;
		border-right: none;
		border-top-color: $white;
	}

	.mapboxgl-popup-content {
		border-bottom-right-radius: 0;
	}
}

.mapboxgl-popup-anchor-left {
	flex-direction: row;

	.mapboxgl-popup-tip {
		align-self: center;
		border-left: none;
		border-right-color: $white;
	}
}

.mapboxgl-popup-anchor-right {
	flex-direction: row-reverse;

	.mapboxgl-popup-tip {
		align-self: center;
		border-right: none;
		border-left-color: $white;
	}
}

.mapboxgl-popup-tip {
	width: 0;
	height: 0;
	border: 10px solid transparent;
	z-index: 1;
}

.mapboxgl-popup-close-button {
	position: absolute;
	right: 0;
	top: 0;
	border: 0;
	border-radius: 0 3px 0 0;
	cursor: pointer;
	background-color: transparent;

	&:hover {
		background-color: $black_5;
	}
}

.mapboxgl-popup-content {
	position: relative;
	background: $white;
	border-radius: 3px;
	box-shadow: 0 1px 2px $black_10;
	padding: 10px 10px 15px;
	pointer-events: auto;
}

.mapboxgl-popup-track-pointer {
	display: none;

	* {
		pointer-events: none;
		user-select: none;
	}
}

.mapboxgl-marker {
	position: absolute;
	top: 0;
	left: 0;
	will-change: transform;
}

.mapboxgl-user-location-dot {
	background-color: $color-dodger_blue;
	width: 15px;
	height: 15px;
	border-radius: 50%;

	&:before {
		background-color: $color-dodger_blue;
		width: 15px;
		height: 15px;
		border-radius: 50%;
		content: "";
		position: absolute;
		animation: mapboxgl-user-location-dot-pulse 2s infinite;
	}

	&:after {
		border-radius: 50%;
		border: 2px solid $white;
		content: "";
		height: 19px;
		left: -2px;
		position: absolute;
		top: -2px;
		width: 19px;
		box-sizing: border-box;
		box-shadow: 0 0 3px $black_35;
	}
}

@-webkit-keyframes mapboxgl-user-location-dot-pulse {
	0% {
		opacity: 1;
	}

	70% {
		opacity: 0;
	}

	to {
		opacity: 0;
	}
}

@-ms-keyframes mapboxgl-user-location-dot-pulse {
	0% {
		opacity: 1;
	}

	70% {
		opacity: 0;
	}

	to {
		opacity: 0;
	}
}

@keyframes mapboxgl-user-location-dot-pulse {
	0% {
		transform: scale(1);
		opacity: 1;
	}

	70% {
		transform: scale(3);
		opacity: 0;
	}

	to {
		transform: scale(1);
		opacity: 0;
	}
}

.mapboxgl-user-location-dot-stale {
	background-color: $color-silver_chalice;

	&:after {
		display: none;
	}
}

.mapboxgl-crosshair {
	cursor: crosshair;

	.mapboxgl-interactive {
		cursor: crosshair;

		&:active {
			cursor: crosshair;
		}
	}
}

.mapboxgl-boxzoom {
	position: absolute;
	top: 0;
	left: 0;
	width: 0;
	height: 0;
	background: $white;
	border: 2px dotted $color-log_cabin;
	opacity: 0.5;
}

@media print {
	.mapbox-improve-map {
		display: none;
	}
}

a.mapboxgl-ctrl-logo {
	width: 88px;
	height: 23px;
	margin: 0 0 -4px -4px;
	display: block;
	background-repeat: no-repeat;
	cursor: pointer;
	overflow: hidden;
	background-image: url($data_uri_24);

	&.mapboxgl-compact {
		width: 23px;
	}
}
